<template>
  <div class="qualification-main">
    <c-nav-bar class="qualification-nav-bar" @clickRight="clickRight">
      <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
      <span slot="center" class="nav-bar-center">
        资质认证
      </span>
      <span slot="right" class="nav-bar-right">
        去首页
      </span>
    </c-nav-bar>
    <div class="qualification-content" ref="content">
      <div>
        <transition name="slide">
          <div class="reasons-failed" v-show="realAuthShow">
            <div class="reasons-title">
              审核不通过原因：
            </div>
            <div class="reason-content">
              <p>1、工作年限没有满半年</p>
              <p>1、工作年限没有满半年</p>
              <p>1、工作年限没有满半年</p>
            </div>
          </div>
        </transition>
        <!-- 基本信息 -->
        <div class="basic-info">
          <div class="header">
            基本信息
          </div>
          <c-cell v-model="jobType.codeName" @clickSelect="clickJobTypeSelect" title="职业类型" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="educationLevel.codeName" @clickSelect="clickEducationLevelSelect" title="学历信息" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="educationDepartment" title="专业名称" :mark="markShow" rightType="input"></c-cell>

          <!-- <c-cell v-model="val.codeName" @clickSelect="baseInfoSelect(val,index)" v-for="(val,index) in baseInfo" :key="index" :title="val.title" :mark="val.mark" :rightType="val.rightType"></c-cell> -->
          <c-cell v-model="addressText" @clickSelect="clickAddressSelect" :title="userAreas.title" :mark="true" :rightType="userAreas.rightType"></c-cell>
        </div>
        <!-- 医护人员工作单位 -->
        <div class="work-units" v-if="PersonnelTypeCode === '01'">
          <div class="header">
            工作单位
          </div>
          <c-cell v-model="get_hospital.hospitalName" @clickSelect="clickSelectWork" title="医院名称" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="get_hospital.levelName" title="医院等级" :mark="false" rightType="text"></c-cell>
          <c-cell v-model="get_hospital.hospitalNatureName" title="医院性质" :mark="false" rightType="text"></c-cell>
        </div>

        <!-- 保险人员工作单位 -->
        <div class="work-units" v-if="PersonnelTypeCode === '02'">
          <div class="header">
            工作单位【临近工作单位】
          </div>
          <c-cell v-model="insuranceAgent.workingPlace.codeName" @clickSelect="clickSelectInsuranceAgentWork" title="单位名称" :mark="true" rightType="select"></c-cell>
          <div class="work-header">
            岗位 <i>*</i>
          </div>
          <chose-item :isShowAdd="false" @clickItem="clickItemTaskPost" :list="InsurerPost"></chose-item>
          <c-cell v-model="insuranceAgent.workingAge.codeName" @clickSelect="clickSelectInsuranceAgentWorkYear" title="本职工作年限" :mark="true" rightType="select"></c-cell>
        </div>

        <!-- 司法人员工作单位 -->
        <div class="work-units" v-if="PersonnelTypeCode === '03'">
          <div class="header">
            工作单位【临近工作单位】
          </div>
          <c-cell v-model="judicialAgent.workingPlace.codeName" @clickSelect="clickSelectInsuranceAgentWork" title="单位名称" :mark="false" rightType="input"></c-cell>
          <c-cell v-model="judicialAgent.workingAge.codeName" @clickSelect="clickSelectInsuranceAgentWorkYear" title="本职工作年限" :mark="true" rightType="select"></c-cell>
        </div>
        <!-- 其他人员的工作单位 -->
        <div class="work-units" v-if="PersonnelTypeCode === '99'">
          <div class="header">
            工作单位【临近工作单位】
          </div>
          <c-cell v-model="otherAgent.workingPlace.codeName" title="单位名称" :mark="false" rightType="input"></c-cell>
          <c-cell v-model="otherAgent.jobTitle.codeName" title="职称" :mark="true" rightType="input"></c-cell>
          <c-cell v-model="otherAgent.workingAge.codeName" @clickSelect="clickSelectOtherAgentWorkYear" title="本职工作年限" :mark="true" rightType="select"></c-cell>
        </div>
        <!-- 司法人员专长 -->
        <div class="specialty-info" v-if="PersonnelTypeCode === '03'">
          <div class="header specialty-header">
            <span>
              专长<i> *</i>
            </span>
            <!-- <span class="type-tip">
              类型说明
            </span> -->
          </div>
          <chose-item :isShowAdd="false" @clickItem="clickJudicialSkillItem" :list="JudicialSkill"></chose-item>
        </div>
        <!-- 职业信息 -->
        <div class="professiona-info" v-if="PersonnelTypeCode === '01'">
          <div class="header">
            职业信息
          </div>
          <c-cell v-model="hospitalJobTitle.jobTitle.codeName" @clickSelect="clickSelectProfessiona('jobTitle')" title="职称" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="hospitalJobTitle.workingAge.codeName" @clickSelect="clickSelectProfessiona('workingAge')" title="本职工作年限" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="hospitalJobTitle.hospitalDepartment.codeName" @clickSelect="clickSelectProfessiona('hospitalDepartment')" title="所在科室" :mark="true" rightType="select"></c-cell>
        </div>
        <!-- 医护人员专长 -->
        <div class="specialty-info" v-if="PersonnelTypeCode === '01'">
          <div class="header specialty-header">
            <span>
              专长<i> *</i>
            </span>
          </div>
          <chose-item @clickAddItem="clickAddItem" @clickItem="clickItem" :list="DoctorSkill"></chose-item>
        </div>
        <!-- 资源医院 -->
        <div class="hospital">
          <c-cell v-model="hospitalText" @clickSelect="clickSelectHospital" title="资源医院" :mark="true" rightType="select"></c-cell>
          <c-cell v-model="receiptAreaText" @clickSelect="clickSelectReciptArea" title="接单地区" :mark="true" rightType="select"></c-cell>
        </div>
        <!-- 任务类型 -->
        <div class="task-type">
          <div class="header task-type-header">
            <span>
              任务类型<i> *</i>
            </span>
            <!-- <span class="type-tip">
              类型说明
            </span> -->
          </div>
          <chose-item @clickAddItem="clickAddItem" @clickItem="clickItemTask" :list="TaskType"></chose-item>
        </div>
        <!-- 证件上传 -->
        <div class="upload-certificate">
          <div class="header">
            证件上传
          </div>
          <upload-img title="学历证照片" :imgList="imgList0" @addImg="addImg($event,'imgList0')" :maxNum="2"></upload-img>
          <upload-img title="执业证照片" :imgList="imgList1" @addImg="addImg($event,'imgList1')" :maxNum="5"></upload-img>
          <upload-img title="其他" :imgList="imgList2" @addImg="addImg($event,'imgList2')" desc="职称证书、工作证照片等，" :maxNum="5"></upload-img>
        </div>
        <!-- 自我评价 -->
        <div class="self-assessment">
          <div class="header">
            自我评价
            <span>{{ curWordsNum }}/100</span>
          </div>
          <van-field rows="5" type="textarea" v-model="userDesc" style="padding-top:0;line-height:20px" placeholder="自我评价(100字以内)" />
        </div>
        <!-- 提价按钮 -->
        <div class="submit-btn">
          <van-button size="large" @click="_submit" class="btn" type="default">
            提交申请
          </van-button>
        </div>
      </div>
    </div>
    <!-- confirm -->
    <c-confirm title="专长添加" @confirm="confirm" text="输入您的专长，限五字以内" confirmBtnText="添加" cancelBtnText="取消" ref="specialtyConfirm">
      <div class="input-content">
        <input type="text" v-model="DoctorSkillText" maxlength="5">
      </div>
    </c-confirm>
    <!-- 职业类型列表 -->
    <van-popup v-model="baseInfoShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        :title="selectTitle"
        :columns="baseInfoColumns"
        @cancel="baseInfoCancel"
        @confirm="baseInfoConfirm"
      />
    </van-popup>

    <!-- 保险人员的工作地点 -->
    <van-popup v-model="insuranceAgentWorkShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="工作地点"
        :columns="CompanyType"
        @cancel="insuranceAgentWorkCancel"
        @confirm="insuranceAgentWorkConfirm"
      />
    </van-popup>

    <!-- 保险人员的工作年限 -->
    <van-popup v-model="insuranceAgentWorkYearShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="工作年限"
        :columns="WorkYear"
        @cancel="insuranceAgentWorkYearCancel"
        @confirm="insuranceAgentWorkYearConfirm"
      />
    </van-popup>

    <!-- 其他人员的工作年限 -->
    <van-popup v-model="otherAgentWorkYearShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="工作年限"
        :columns="WorkYear"
        @cancel="otherAgentWorkYearCancel"
        @confirm="otherAgentWorkYearConfirm"
      />
    </van-popup>
    <!-- 职业类型 -->
    <van-popup v-model="jobTypeShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="职业类型"
        :columns="PersonnelType"
        @cancel="jobTypeCancel"
        @confirm="jobTypeConfirm"
      />
    </van-popup>
    <!-- 学历信息 -->
    <van-popup v-model="educationLevelShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="学历信息"
        :columns="Education"
        @cancel="educationLevelCancel"
        @confirm="educationLevelConfirm"
      />
    </van-popup>
    <!-- 医护人员的职称 -->
    <van-popup v-model="jobTitleShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="职称"
        :columns="HospitalTitle"
        @cancel="hospitalTitleCancel"
        @confirm="hospitalTitleConfirm"
      />
    </van-popup>
    <!-- 医护人员的工作年限 -->
    <van-popup v-model="workingAgeShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="工作年限"
        :columns="WorkYear"
        @cancel="workingAgeCancel"
        @confirm="workingAgeConfirm"
      />
    </van-popup>
    <!-- 医护人员的职称 -->
    <van-popup v-model="hospitalDepartmentShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="所在科室"
        :columns="hospitalDepartment"
        @cancel="hospitalDepartmentCancel"
        @confirm="hospitalDepartmentConfirm"
      />
    </van-popup>
    <!-- 其他人员的职称 -->
    <!-- <van-popup v-model="otherAgentJobTitleShow" :closeOnClickOverlay="false" position="bottom">
      <van-picker
        :showToolbar="true"
        valueKey="codeName"
        title="职称"
        :columns="PersonnelType"
        @cancel="otherAgentJobTitleCancel"
        @confirm="otherAgentJobTitleConfirm"
      />
    </van-popup> -->
    <!-- <van-area :area-list="AreaList" :columns-num="2" title="选择地区" /> -->
    <!-- address -->
    <c-address @close="closeAddress" ref="address"></c-address>
    <router-view></router-view>
  </div>
</template>
<script>
// import cell from './cell'
import choseItem from './choseItem'
import uploadImg from './uploadImg'
import BScroll from 'better-scroll'
import { mapGetters, mapActions } from 'vuex'
export default {
  components: {
    // cell,
    'chose-item': choseItem,
    'upload-img': uploadImg
  },
  data() {
    return {
      realAuthShow: false,
      selectTitle: '',
      curPersonnelType: 'codeCode',
      PersonnelTypeCode: '01',
      baseParams: 'PersonnelType,Education,HospitalTitle,DepartmentType,DoctorSkill,TaskType,CompanyType,JudicialSkill,InsurerPost,ImageType',
      allData: [],
      scroll: '',
      userDesc: '', // 自我评价
      Education: [], // 学历信息
      HospitalTitle: [], // 医护人员职称
      PersonnelType: [], // 职业类型
      DepartmentType: [], // 科室类型
      DoctorSkill: [], // 医护人员技能
      TaskType: [], // 任务类型
      WorkYear: [], // 工作年限
      AreaList: [],// 所在地区
      InsurerPost: [], // 保险人员工作岗位
      CompanyType: [], // 保险公司
      JudicialSkill: [],// 司法人员专长
      educationDepartment: '', //专业名称
      educationLevel: {}, // 学历
      hospitalDepartment: [], //所在科室
      markShow: true,
      jobType: {// 职业类型
        codeCode: '01',
        codeName: '医护人员'
      },
      baseInfo: [{
        title: '职业类型',
        mark: true,
        rightType: 'select',
        codeCode: '01',
        codeName: '医护人员',
        type: 'PersonnelType'
      },{
        title: '学历信息',
        mark: true,
        rightType: 'select',
        codeCode: '',
        codeName: '',
        type: 'Education'
      },{
        title: '专业名称',
        mark: true,
        rightType: 'input',
        codeCode: '',
        codeName: '',
        type: ''
      }],
      userAreas: {
        title: '常住地区',
        mark: true,
        rightType: 'select',
      },
      userAreasList: [],
      professionaInfo: [{
        title: '职称',
        mark: true,
        rightType: 'select',
        codeCode: '',
        codeName: '',
        type: 'HospitalTitle'
      },{
        title: '本职工作年限',
        mark: true,
        rightType: 'select',
        codeCode: '',
        codeName: '',
        type: 'WorkYear'
      },{
        title: '所在科室',
        mark: true,
        rightType: 'select',
        codeCode: '',
        codeName: '',
        type: 'DepartmentType'
      }],
      receiptArea: {
        codeCode: '01',
        title: '接单地区',
        codeName: '接单地区',
        rightType: 'select',
        type: 'receiptArea',
        mark: true,
      },
      baseInfoShow: false,
      baseInfoIndex: '', // 默认选择的
      baseInfoColumns: [],
      imgList0: [],// 学历照片集
      imgList1: [],
      imgList2: [],
      DoctorSkillText: '', //添加擅长专业
      hospitalJobTitle: {
        workingAge: {},
        jobTitle: {},
        hospitalDepartment: {}
      },
      insuranceAgent: { // 保险人员
        workingAge: {},
        workingPlace: {}
      },
      judicialAgent: { // 司法人员
        workingAge: {},
        workingPlace: {}
      },
      otherAgent: { // 其他人员人员
        workingAge: {},
        jobTitle: {},
        workingPlace: {}
      },
      insuranceAgentWorkShow: false, //保险人员的工作单位picker
      insuranceAgentWorkYearShow: false, //保险人员的工作单位picker
      otherAgentWorkYearShow: false, // 其他人员的工作年限picker
      otherAgentJobTitleShow: false, // 其他工作人员的职称picker
      jobTypeShow: false, // 职业类型
      educationLevelShow: false, // 学历
      jobTitleShow: false, // 医护人员职称
      workingAgeShow: false,
      hospitalDepartmentShow: false
    }
  },
  computed: {
    ...mapGetters(['get_receiptAreaList','get_hospitalList','get_hospital']),
    receiptAreaText() {
      if(this.get_receiptAreaList.length) {
        return `共${this.get_receiptAreaList.length}个`
      } else {
        return ''
      }
    },
    addressText() {
      return this.userAreasList.reduce((prv, next) => prv + next.codeName ,'')
    },
    hospitalText() {
      if(this.get_hospitalList.length) {
        return `共${this.get_hospitalList.length}个`
      } else {
        return ''
      }
    },
    curWordsNum() {
      return Math.min(this.userDesc.length, 100)
    }
  },
  created() {
    this.init() // 初始化
    this._getYear(0,80)
    console.log(this.get_hospital)
  },
  mounted() {
    setTimeout(() => {
      this.scroll = new BScroll(this.$refs.content,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },20)
  },
  methods: {
    ...mapActions(['change_Province','change_City','change_CountyCode']),
    init() {
      this.$http.get('/api/support/listCodesByTypes', {params: {codeTypes: this.baseParams}}).then(res => {
        if (!res.data.success) {
          return
        }
        let _data = res.data.data
        console.log(_data)
        for(let key in _data) {
          if (key === 'DoctorSkill') {
            this.DoctorSkill = _data[key].filter((item) => {
              return item.codeCode !== '99'
            })
          } else {
            this[key] = _data[key]
          }
        }
      }).catch((err) => {
        console.log(err)
      })
      // 获取省市区
      this.$http.get('/api/support/listCodesByTypes', {params: {codeTypes: 'Province,City,CountyCode'}}).then(res => {
        if (!res.data.success) {
          return
        }
        let _data = res.data.data
        for(let key in _data) {
          this[`change_${key}`](_data[key])
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    _getYear(min, max) {
      let _arr = []
      let _curYear = new Date().getFullYear()
      for(let i = min; i <= max; i++) {
        let _y = _curYear - i
        _arr.push({codeCode: _y,codeName: _y + '年'})
      }
      this.WorkYear = _arr
    },
    clickAddressSelect() {
      this.$refs.address.show()
    },
    clickSelectInsuranceAgentWork() { // 保险人员的工作单位picker显示
      this.insuranceAgentWorkShow = true
    },
    clickSelectInsuranceAgentWorkYear() { // 保险人员的工作时间picker显示
      this.insuranceAgentWorkYearShow = true
    },
    clickSelectOtherAgentWorkYear() { // 其他人员的工作年限picker显示
      this.otherAgentWorkYearShow = true
    },
    clickSelecOtherAgentJobTitle() { // 其他人员的职称picker显示
      this.otherAgentJobTitleShow = true
    },
    clickJobTypeSelect() {
      this.jobTypeShow = true
    },
    clickEducationLevelSelect() {
      this.educationLevelShow = true
    },
    baseInfoSelect($value, $index) { // 基本信息的select选择
      this.paramsType = 'baseInfo'
      this.baseInfoColumns = this[$value.type]
      this.selectTitle = $value.title
      this.baseInfoIndex = $index
      this.baseInfoShow = true
    },
    baseInfoConfirm(value) {
      let _index = this.baseInfoIndex
      let _data = (this[this.paramsType][_index]);
      if(value.codeCode !== _data.codeCode) {
        if(value.codeType === 'PersonnelType') {
          this.PersonnelTypeCode = value.codeCode
        }
        this[this.paramsType].splice(_index, 1, Object.assign({}, _data, value))
      }
      this.baseInfoShow = false
    },
    baseInfoCancel() {
      this.baseInfoShow = false
    },
    clickSelectWork($value, $index) {
      console.log($value, $index)
      this.$router.push('/qualification/seach/addHospital')
    },
    clickSelectProfessiona($value) {
      this[$value + 'Show'] = true
      // this.paramsType = 'professionaInfo'
      // this.baseInfoColumns = this[$value.type]
      // this.selectTitle = $value.title
      // this.baseInfoIndex = $index
      // this.baseInfoShow = true
      // console.log($value, $index)
    },
    clickSelectHospital() { // 资源医院
      this.$router.push('/qualification/hospital')
    },
    clickSelectReciptArea() { // 接单地区
      this.$router.push('/qualification/receiptArea')
    },
    clickItem($value, $index) { // 医护人员的专长
      console.log($value, $index)
      $value.isChose = !$value.isChose
      this.DoctorSkill.splice($index, 1, $value)
    },
    clickAddInsuranceItem($value, $index) { // 保险人员的岗位
      $value.isChose = !$value.isChose
      this.InsurerPost.splice($index, 1, $value)
    },
    clickJudicialSkillItem($value, $index) { // 司法人员专长
      $value.isChose = !$value.isChose
      this.JudicialSkill.splice($index, 1, $value)
    },
    clickItemTask($value, $index) { // 接单的选择
      $value.isChose = !$value.isChose
      this.TaskType.splice($index, 1, $value)
    },
    clickAddItem() {
      this.$refs.specialtyConfirm.show()
    },
    confirm() {
      if(!this.DoctorSkillText) {
        this.$toast('填写信息不能为空')
        return
      }
      this.$refs.specialtyConfirm.hide()
      this.DoctorSkill.push({ codeCode: '99', codeName: this.DoctorSkillText, isChose: true})
    },
    addImg(obj,params) {
      console.log(obj,params)
      this[params].push(obj)
    },
    clickItemTaskPost($value, $index) { // 保险人员的工作岗位
      $value.isChose = !$value.isChose
      this.InsurerPost.splice($index, 1, $value)
    },
    insuranceAgentWorkYearConfirm(value) { // 保险人员的工作时间
      console.log(value)
      this.insuranceAgent.workingAge = value
      this.insuranceAgentWorkYearCancel()
    },
    insuranceAgentWorkYearCancel() {
      this.insuranceAgentWorkYearShow = false
    },
    otherAgentJobTitleConfirm(value) {// 其他人员职称的picker取消
      this.otherAgent.jobTitle = value
      this.otherAgentJobTitleCancel()
    },
    otherAgentJobTitleCancel() {// 其他人员职称的picker取消
      this.otherAgentJobTitleShow = false
    },
    otherAgentWorkYearConfirm(value) { // 其他人员工作年限的picker确定
      this.otherAgent.workingAge = value
      this.otherAgentWorkYearCancel()
    },
    otherAgentWorkYearCancel() { // 其他人员工作年限的picker取消
      this.otherAgentWorkYearShow = false
    },
    hospitalTitleCancel() {
      this.jobTitleShow = false
    },
    hospitalTitleConfirm(value) {
      this.hospitalJobTitle.jobTitle = value
      this.hospitalTitleCancel()
    },
    educationLevelConfirm(value) { // 职业类型
      if(value.codeCode === '01') {
        this.markShow = false
      }
      this.educationLevel =value
      this.educationLevelCancel()
    },
    educationLevelCancel() {
      this.educationLevelShow = false
    },
    jobTypeConfirm(value) { // 职业类型
      this.jobType =value
      this.jobTypeCancel()
    },
    jobTypeCancel() {
      this.jobTypeShow = false
    },
    hospitalDepartmentConfirm(value) {
      this.hospitalJobTitle.hospitalDepartment = value
      this.hospitalDepartmentCancel()
    },
    hospitalDepartmentCancel() {
      this.hospitalDepartmentShow = false
    },
    workingAgeConfirm(value) {
      this.hospitalJobTitle.workingAge = value
      this.workingAgeCancel()
    },
    workingAgeCancel() {
      this.workingAgeShow = false
    },
    insuranceAgentWorkConfirm(value) {
      console.log(value)
      this.insuranceAgent.workingPlace = value
      this.insuranceAgentWorkCancel()
    },
    insuranceAgentWorkCancel() {
      this.insuranceAgentWorkShow = false
    },
    _checkSubmit(callback) {
      // 基本信息
      let _params = {}
      if(!this.jobType.codeCode) {
        this.$toast('请正确填写职业类型')
        return
      }
      if(!this.educationLevel.codeCode) {
        this.$toast('请正确填写学历信息')
        return
      }
      if(this.educationLevel.codeCode !== '01' && !this.educationDepartment) {
        this.$toast('请正确填写专业名称')
        return
      }
      if(this.userAreasList && this.userAreasList.length !== 2) {
        this.$toast('请正确填写常住地区')
        return
      }
      _params = {
        jobType: this.jobType.codeCode,
        educationLevel: this.educationLevel.codeCode,
        educationDepartment: this.educationDepartment,
        provinceCode: this.userAreasList[0].codeCode,
        cityCode: this.userAreasList[1].codeCode
      }
      switch(this.PersonnelTypeCode) {
      case '01':
        // 工作单位 非必填项
        if(!this.get_hospital || (this.get_hospital && !this.get_hospital.hospitalCode)) {
          this.$toast('请正确填写工作单位')
          return false
        } else {
          _params.hospitalCode = this.get_hospital.hospitalCode
          _params.tempHospitalLevel = this.get_hospital.levelName
          _params.tempHospitalNature = this.get_hospital.hospitalNatureName
          _params.tempHospitalProvinceCode = this.get_hospital.provinceCode
          _params.tempHospitalCityCode = this.get_hospital.cityCode
        }
        if(!this.hospitalJobTitle.jobTitle.codeCode) {
          this.$toast('职称不能为空！')
          return
        }
        if(!this.hospitalJobTitle.workingAge.codeCode) {
          this.$toast('本职工作年限不能为空！')
          return
        }
        // if(!this.hospitalJobTitle.hospitalDepartment.codeCode) {
        //   this.$toast('所在科室不能为空！')
        //   return
        // }
        _params.hospitalJob = this.hospitalJobTitle.jobTitle.codeCode
        _params.startJobYear = this.hospitalJobTitle.workingAge.codeCode
        _params.hospitalDepartment = this.hospitalJobTitle.hospitalDepartment.codeCode
        // 专长
        var isChoseDocSkill = this.DoctorSkill.filter(item => item.isChose)
        if(!isChoseDocSkill || !isChoseDocSkill.length) {
          this.$toast('专长信息不能为空')
          return false
        }
        var _hospitalSkills = []
        var _hospitalOtherSkills = []
        isChoseDocSkill.forEach(item => {
          if(item.codeCode === '99') {
            _hospitalOtherSkills.push(item.codeName)
          } else {
            _hospitalSkills.push(item.codeCode)
          }
        })
        _params.hospitalSkills = _hospitalSkills.join(',')
        _params.hospitalOtherSkills = _hospitalOtherSkills.join(',')
        break;
      case '02':
        // 保险人员的单位名称
        if(!this.insuranceAgent.workingPlace.codeCode) {
          this.$toast('单位名称不能为空！')
          return
        }
        _params.insCompanyCode = this.insuranceAgent.workingPlace.codeCode
        // 保险人员的岗位
        var InsurerPostFilter = this.InsurerPost.filter(item => item.isChose)
        if(!InsurerPostFilter.length) {
          this.$toast('岗位不能为空！')
          return
        } else {
          var insCompanyJob = []
          InsurerPostFilter.forEach(item => {
            insCompanyJob.push(item.codeCode)
          })
        }
        _params.insCompanyJob = insCompanyJob.join(',')
        // 本职工作年限
        if(!this.insuranceAgent.workingAge.codeCode) {
          this.$toast('本职工作年限不能为空！')
          return
        } else {
          _params.startJobYear = this.insuranceAgent.workingAge.codeCode
        }
        break;
      case '03':
        // 工作单位
        if(this.judicialAgent.workingPlace.codeName) {
          _params.lawInstituteName = this.judicialAgent.workingPlace.codeName
        }
        if(!this.judicialAgent.workingAge.codeCode) {
          this.$toast('本职工作年限不能为空！')
          return
        } else {
          _params.startJobYear = this.judicialAgent.workingAge.codeCode
        }
        // 司法人员专长
        var JudicialSkillFilter = this.JudicialSkill.filter(itme => itme.isChose)
        if(!JudicialSkillFilter.length) {
          this.$toast('专长不能为空！')
          return
        } else {
          _params.lawInstituteSkills = JudicialSkillFilter.join(',')
        }
        break;
      case '99':
        // otherAgent: { // 其他人员人员
        //   workingAge: {},
        //   jobTitle: {},
        //   workingPlace: {}
        // },
        if(this.otherAgent.workingPlace) {
          _params.lawInstituteName = this.otherAgent.workingPlace.codeName
        }
        if(this.otherAgent.jobTitle.codeName) {
          _params.jobTitle = this.otherAgent.jobTitle.codeName
        }
        if(!this.otherAgent.workingAge.codeCode) {
          this.$toast('本职工作年限不能为空！')
        } else {
          _params.startJobYear = this.otherAgent.workingAge.codeCode
        }
        // if(otherAgent) {

        // }
        break;
      }
      // 资源医院
      if(!this.get_hospitalList.length) {
        this.$toast('请选择资源医院')
        return false
      } else {
        var userHospitalResources = []
        this.get_hospitalList.forEach(item => {
          userHospitalResources.push({
            hospitalCode: item.hospitalCode
          })
        })
        _params.userHospitalResources = userHospitalResources
      }
      // 接单地区
      if(!this.get_receiptAreaList.length) {
        this.$toast('请选择接单地区')
        return false
      } else {
        let userAreas = []
        this.get_receiptAreaList.forEach(item => {
          userAreas.push({
            cityCode: item.cityCode,
            provinceCode: item.provinceCode
          })
        })
        _params.userAreas = userAreas
      }
      // 任务类型
      let isChoseTaskType = this.TaskType.filter(item => item.isChose)
      console.log(isChoseTaskType)
      if(!isChoseTaskType.length) {
        this.$toast('请选择任务类型')
        return false
      } else {
        var roleTypes = []
        isChoseTaskType.forEach(item => {
          roleTypes.push(item.codeCode)
        })
        _params.roleTypes	 = roleTypes.join(',')
      }
      // 证件上传
      let userQualificationVerifyImages = []
      if(this.imgList0 && !this.imgList0.length) {
        this.$toast('请上传学历证照片')
        return
      } else {
        this.imgList0.forEach((item,index) => {
          userQualificationVerifyImages.push({
            imageId: item.fileId,
            imageType: '01',
            imageSerialNo: index + 1
          })
        })
        // userQualificationVerifyImages.push({
        //   imageId: img0.join(',')
        // })
      }
      if(this.imgList1 && !this.imgList1.length) {
        this.$toast('请上传执业证照片')
        return
      } else {
        this.imgList1.forEach((item,index) => {
          userQualificationVerifyImages.push({
            imageId: item.fileId,
            imageType: '02',
            imageSerialNo: index + 1
          })
        })
      }
      if(this.imgList2 && !this.imgList2.length) {
        this.$toast('请上传其他照片')
        return
      } else {
        this.imgList2.forEach((item,index) => {
          userQualificationVerifyImages.push({
            imageId: item.fileId,
            imageType: '03',
            imageSerialNo: index + 1
          })
        })
        // let img2 = []
        // this.imgList2.forEach(item => {
        //   img2.push(item.fileId)
        // })
        // userQualificationVerifyImages.push({
        //   imageId: img2.join(',')
        // })
      }
      _params.userQualificationVerifyImages = userQualificationVerifyImages
      _params.userDesc = this.userDesc
      callback(_params)
    },
    _submit() {
      this._checkSubmit((params) => {
        console.log(params)
        this.$http.post('/api/userQualification/submit',params).then(res => {
          if (res.data.success){
            this.$toast('提交成功')
          } else {
            this.$toast(res.data.errorMessage)
          }
        }, err => {
          console.log(err)
        })
      })
    },
    closeAddress(data) {
      this.$refs.address.hide()
      if(!data) {
        return
      }
      console.log(data)
      this.userAreasList = data
    },
    clickRight() {
      this.$router.push('/home')
    }
  }
}
</script>
