          color:$color-theme;
        }
        .type-tip{
          font-size: 16px;
          color:rgb(153, 153, 153);
          font-weight: bold;
          padding-right:2px;
        }
      }
    }
    // 证件上传
    .upload-certificate {
      margin-top: 10px;
      background: rgb(255,255,255);
      padding-bottom: 22px;
      .header {
        position: relative;
        &:after {
          position: absolute;
          content: ' ';
          left: 15px;
          right: 15px;
          bottom: 0px;
          height: 1px;
          border: none;
          background:rgb(204,204,204);
          @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
            transform: scaleY(0.333)
          }
          @media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2) {
            transform: scaleY(.5)
          }
        }
      }
    }
    // 自我评价
    .self-assessment{
      margin-top: 10px;
      background: rgb(255,255,255);
    }
    // 提交按钮
    .submit-btn {
      padding: 0 15px;
      margin-top: 40px;
      height: 45px;
      overflow: hidden;
      .btn {
        border-radius: 22px;
        color:#fff;
        font-size:18px;
        height: 44px;
        line-height: 44px;
        background-image: linear-gradient(to right, $color-theme, $color-gradient-end);
