<template>
  <div class="choseItem">
    <div class="item" @click="clickItem(val, index)" :class="{active:val.isChose}" v-for="(val, index) in list" :key="index">
      {{ val.codeName }}
    </div>
    <div class="item" v-show="list.length<6 && isShowAdd" @click="addItem">
      <i class="iconfont icon-add"></i>
      其他
    </div>
  </div>
</template>
<script>
export default {
  props: {
    maxLength: {
      type: Number,
      default: 6
    },
    list: {
      type: Array,
      default: () => []
    },
    isShowAdd: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    clickItem($value, $index) {
      this.$emit('clickItem', $value, $index)
    },
    addItem() {
      this.$emit('clickAddItem')
    }
  }
}
</script>
<style lang="scss" scoped>
.choseItem{
  display: flex;
  padding:0 5vw;
  flex-wrap: wrap;
  .item{
    width:28vw;
    text-align: center;
    height: 38px;
    line-height: 36px;
    border: 1px solid rgb(234,84,4);
    font-size:16px;
    color:rgb(234,84,4);
    border-radius: 18px;
    margin-left: 3vw;
    &.active{
      background-image: linear-gradient(to right, (rgba(243,120,54, 1)), (rgba(251,170,98, 1)));
      color:rgb(256,256,256);
      border: none;
    }
  }
  .item:nth-child(3n+1) {
    margin-left: 0vw;
    margin-bottom: 15px;
  }
}
</style>
