<template>
  <div class="upload-img-main">
    <div class="header-num">
      <span class="header">
        {{ title }}<i> *</i>
      </span>
      <span class="num">
        ({{ this.desc }}{{ curNum }}/{{ maxNum }})
      </span>
    </div>
    <div class="img-list">
      <div class="img-item" v-for="(val, index) in imgList" :key="index">
        <img :src="baseUrl + val.fileId">
      </div>
      <div class="add-img" v-show="curNum < maxNum">
        <p class="top">
          <i class="iconfont icon-iconset0186"></i>
        </p>
        <p>
          添加{{ title }}
        </p>
        <input type="file" accept="image/*" @change="onloadImg">
        <div class="loading" v-show="isShowLoading">
          <van-loading class="loading-icon" type="spinner" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const BASEURL = 'http://116.62.179.17:8080/api/file/get?id='
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    maxNum: {
      type: Number,
      default: 0
    },
    desc: {
      type: String,
      default: ''
    },
    imgList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      baseUrl: BASEURL,
      isShowLoading: false
    }
  },
  computed: {
    curNum() {
      return this.imgList.length
    }
  },
  methods: {
    upload(formData) {
      console.log(formData)
      this.isShowLoading = true
      this.$http.post(
        '/api/file/upload',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
      ).then(response => {
        console.log(response)
        this.isShowLoading = false
        if(response.data.success) {
          this.$emit('addImg',{fileId: response.data.data.fileId})
        } else {
          this.$toast(response.data.errorMessage)
        }
      },err => {
        this.isShowLoading = false
        console.log(err)
      })
    },
    onloadImg(e) {
      let file = (e.target.files[0])
      e.target.value = ''
      if (file.size/1024>1024*10) {
        this.$toast('上传图片不应超过10M')
        return
      } else if(file.size/1024<1024) {
        let formData = new FormData()
        formData.append('file', file)
        this.upload(formData)
      } else {
        this.compress(file,this.upload)
      }
    },
    compress(file,callback) {
      let _self = this
      let ready = new FileReader()
      ready.readAsDataURL(file);
      ready.onload=function(){
        var img = new Image()
        img.src = this.result
        img.onload = function() {
          var that = this
          var w = that.width
          var h = that.height
          var scale = w/h
          var canvas = document.createElement('canvas')
          var ctx = canvas.getContext('2d')
          let canvasWidth = Math.min(w,600)
          canvas.width = canvasWidth
          canvas.height = parseInt(canvasWidth/scale)
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
          var base64 = canvas.toDataURL('image/jpeg',1.0);
          var blobDate = _self.uriToBlob(base64)
          let formData = new FormData()
          formData.append('file', blobDate,'file_' + Date.parse(new Date()) + '.png')
          callback(formData)
        }
      }
    },
    uriToBlob: function(urlData) {
      var arr = urlData.split(','), mime = arr[0].match(/:(.*?);/)[1],bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    }
  }
}
</script>
<style lang="scss" scoped>
// @import "../../css/variable.scss";
.upload-img-main {
  padding: 0 20px;
  .header-num{
    font-size: 16px;
    padding: 15px 0;
    .header{
      i{
        color: $color-theme;
        font-size: 16px;
      }
    }
    .num{
      font-size: 14px;
      color: rgb(153, 153, 153);
    }
  }
  .img-list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .img-item{
      width: 40vw;
      height: 25vw;
      border-radius: 6px;
      margin-bottom: 10px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100px;
      }
    }
    .add-img {
      display: flex;
      align-items: center;
      width: 40vw;
      height: 25vw;
      background: rgb(245,245,245);
      border-radius: 6px;
      color:rgb(204,204,204);
      flex-direction:column;
      justify-content: center;
      position: relative;
      .loading {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        .loading-icon {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
      p {
        font-size: 14px;
        text-align: center;
        i {
          font-size: 26px;
        }
      }
      .top{
        margin-bottom: 2px;
      }
      input{
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0;
        cursor: pointer;
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>

